html {
  --blue: #00acdc;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: inline-block;
}

.header__logo {
  width: 40px;
  height: 40px;
  padding: 10px 0;
  display: block;
}

.nav__list {
  flex-wrap: nowrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-align: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: var(--blue);
}

.nav__item {
  text-transform: uppercase;
  align-items: center;
  margin-left: 20px;
  font-weight: 500;
  display: inline-block;
  position: relative;
}

.nav__item:first-child {
  margin: 0;
}

.is-active {
  color: var(--blue);
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
